.wrapper{
    display: flex;
    align-items: center;
    padding: 15px;
    border-bottom: 1px  dashed var(--yellow-theme-main-color);
    h2{
        font-size: 25px;
        font-weight: 500;
        letter-spacing: 1.5px;
        line-height: 1rem;
        text-transform: capitalize;
        color: var(--yellow-theme-main-color);
        display: flex;
        flex-grow: 1;
    }
    span{
        svg{
        color: var(--yellow-theme-main-color);
        }
    }

}