.home{
    position: relative;
    position: relative;
    height: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;// need to remove to get slow effect.
  
    &_text-wrapper{
        display: flex;
        // align-content: center;
        // flex-direction: column;
        justify-content: center;
      h1{
        color: var(--yellow-theme-main-color);
        margin: 0px;
        font-weight: 500;
        font-size: 4rem;
        line-height: 7rem;
        position: relative;
        overflow: hidden;
        white-space: nowrap;
        animation:tyingEffectAnimation 2.5s steps(30,end);

      }
    }
    &_contact-me{
       display: flex;
       justify-content: center;
       margin-top:15px ;
       button{
        cursor: pointer;
        padding: 10px 70px;
        background: transparent;
        // background-color: var(--yellow-theme-main-color);
        border: 1.5px solid var(--yellow-theme-main-color);
        border-radius: 4px;
        font-weight:500 ;
        &:hover{
            background: var(--yellow-theme-main-color);
            color: #000000;
        }

       }

    }
}
@media only screen and (max-width : 550px)
{
    .home_text-wrapper h1{
        font-size: 2rem;
        line-height: 6rem;
    }

}
@media only screen and (min-width : 551px) and (max-width : 768px)
{
    .home_text-wrapper h1{
        font-size: 3rem;
        line-height: 7rem;
    } 
    
}
@media only screen and (min-width : 769) and (max-width : 990px)
{
    .home_text-wrapper h1{
        font-size: 3.5rem;
        line-height: 8rem;
    }  
}
@keyframes tyingEffectAnimation
{
    from{
        width:0;
    }
    to{
        width: 70%;
    }
}